.modal {
  border-radius: 0;
  margin: 20% 0;
  padding: 0;
}
.modal-body {
  padding: 0;
  margin: 0;
  text-align: center;
}
.alert {
  margin: 0;
  border-radius: 0;
  border: 1.5px solid black;
  color: #000;
  font-size: 1rem;
  font-family: 'Montserrat', sans-serif;
}
.alert .MuiSvgIcon-root {
  font-size: 1.2rem;
  margin-right: .5rem;
}
.alert-success {
  background-color: #43a047;
}
.alert-danger {
  background-color: #d32f2f;
}
.alert .alert-dismissible {
  padding-right: 2rem;
}
.alert .close {
  font-size: 2rem;
  padding: .50rem 1.25rem;
}
@media only screen and (max-width: 1024px) {
  .modal {
    margin-top: 60%;
  }
  .MuiCardActions-root .MuiButtonBase-root, .MuiCardActions-root .MuiButtonBase-root:focus {
    width: 100%;
    margin: .5rem 0;
    border-radius: 0;
    background-color: rgba(0,0,0,.2);
    color: #fff;
  }
}